import React from 'react';
import './MainContent.css';
import Card from '../Card/Card';
import ShoppingCart from '../ShoppingCart/ShoppingCart';
import Image5cut from '../assets/Image1-5-cut.png';
import { getCategory } from '../api/category';
import Image1 from '../assets/Image1-5.jpg';
import Image2 from '../assets/Image2.jpg';
import Image3 from '../assets/Image3.jpg';
import Image4 from '../assets/Image4.png';
import Image7 from '../assets/Image7.png';
import Image6 from '../assets/Image6.jpg'





class MainContent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            products: [
                { src: Image1, title: "Apollo Running Short", price: "$50.00" },
                { src: Image2, title: "Apollo Running Short", price: "$50.00" },
                { src: Image3, title: "Apollo Running Short", price: "$50.00" },
                { src: Image4, title: "Apollo Running Short", price: "$50.00" },
                { src: Image7, title: "Apollo Running Short", price: "$50.00" },
                { src: Image6, title: "Apollo Running Short", price: "$50.00" }
            ]
        }
    }


    render() {
        return (
            <div style={{ marginTop: "80px" }}>
                <ShoppingCart title="Apollo" type="Running Short" price="$50.00" size1="S" size2="M" quantity="1" imgSrc={Image5cut} />
                <div>
                    <div>
                        <div className="category">{this.state.name}</div>
                    </div>
                    <div className="item-row" style={{ marginTop: "60px" }} >
                        {
                            this.state.products.map((item) => {
                                return (<Card title={item.name} price={item.prices[0].amount} currency={item.prices[0].currency.symbol} imgSrc={item.gallery[0]} key={item.id} />)
                            })
                        }
                    </div>
                </div>
            </div>
        )
    }
}




export default MainContent;